import React, { Suspense, lazy } from 'react'
import { Switch, Route, Redirect, HashRouter } from 'react-router-dom'
import 'firebase/messaging'
import { connect } from 'react-redux'
import Spinner from './components/@vuexy/spinner/Loading-spinner'
import { ContextLayout } from './utility/context/Layout'
const Dashboard = lazy(() => import('./views/mydashboard/Dashboard'))
const NomineeList = lazy(() => import('./views/Nominee/NomineeList'))
const Spends = lazy(() => import('./views/Spends/SpendsList'))
const Vault = lazy(() => import('./views/DocumentVault/DocumentVault'))
const PasswordVault = lazy(() => import('./views/PasswordVault/PasswordVault'))
const Diary = lazy(() => import('./views/Diary/Diary'))
const MailConfirm = lazy(() => import('./views/Login/MailConfirm'))
const Portfolio = lazy(() => import('./views/Portfolio/Portfolio'))
const Sender = lazy(() => import('./views/ContactUs/Sender'))
const PrivacyTerms = lazy(() => import('./views/PrivacyTerms/PrivacyTerms'))
const AddLiability = lazy(() => import('./views/Liability/Liability'))
const UpdateProfile = lazy(() => import('./views/user/editprofile/Edit'))
const Subscription = lazy(() => import('./views/user/editprofile/Payments'))
const home = lazy(() => import('./views/pages/LandingPage/LandingPage'))
const AddAssets = lazy(() => import('./views/UserAssets/Assets'))

// Set Layout and Component Using App Route
const RouteConfig = ({ component: Component, fullLayout, ...rest }) => (
  <Route
    {...rest}
    render={(props) => {
      let user1 = localStorage.getItem('logInUserData')
      if (
        user1 === null &&
        !(
          props.location.pathname.includes('home') ||
          props.location.pathname.includes('register')
        )
      ) {
        return <Redirect to="/home" />
      }

      return (
        <ContextLayout.Consumer>
          {(context) => {
            let LayoutTag =
              fullLayout === true
                ? context.fullLayout
                : context.state.activeLayout === 'horizontal'
                ? context.horizontalLayout
                : context.VerticalLayout
            return (
              <LayoutTag {...props} permission={props.user}>
                <Suspense fallback={<Spinner />}>
                  <Component {...props} />
                </Suspense>
              </LayoutTag>
            )
          }}
        </ContextLayout.Consumer>
      )
    }}
  />
)
const mapStateToProps = (state) => {
  return {
    user: state.auth.login.userRole
  }
}

const AppRoute = connect(mapStateToProps)(RouteConfig)

const AppRouter = (props) => {
  return (
    // Set the directory path if you are deploying in sub-folder
    <HashRouter basename="/#">
      <Switch>
        <AppRoute exact path="/dashboard" component={Dashboard} />
        <AppRoute path="/nominee/list" component={NomineeList} />
        <AppRoute path="/spends" component={Spends} />
        <AppRoute path="/assets/add" component={AddAssets} />
        <AppRoute path="/vault/document" component={Vault} />
        <AppRoute path="/vault/password" component={PasswordVault} />
        <AppRoute
          path="/emailconfirmation/:userkey"
          component={MailConfirm}
          fullLayout
        />
        <AppRoute path="/diary" component={Diary} />
        <AppRoute path="/diary/:filter" component={Diary} />
        <AppRoute
          path="/diary"
          exact
          component={() => <Redirect to="/diary" />}
        />
        <AppRoute path="/portfolio" component={Portfolio} />
        <AppRoute path="/contactUs" component={Sender} />
        <AppRoute path="/privacyTerms" component={PrivacyTerms} />
        <AppRoute path="/liability/add" component={AddLiability} />
        <AppRoute
          exact
          path="/user/profile"
          component={(props) => <UpdateProfile {...props} />}
        />
        <AppRoute
          exact
          path="/user/feed"
          component={(props) => <UpdateProfile {...props} />}
        />
        <AppRoute
          exact
          path="/user/notify"
          component={(props) => <UpdateProfile {...props} />}
        />
        <AppRoute
          exact
          path="/user/upgrade"
          component={(props) => <UpdateProfile {...props} />}
        />
        <AppRoute
          path="/pages/subscription"
          component={Subscription}
          fullLayout
        />
        <AppRoute path="/home" component={home} />
        <AppRoute path="/" component={home} />
      </Switch>
    </HashRouter>
  )
}

export default AppRouter
